import * as Yup from "yup";
import PropTypes from "prop-types";
// form
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import React, { useState, useEffect } from "react";
// @mui
import {
  Box,
  Card,
  Stack,
  Button,
  Collapse,
  Typography,
  Alert,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { useSnackbar } from "notistack";
import cardValidator from "card-validator";
// components
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
  RHFCardField, // Create this custom field for card inputs
} from "../../../../components/hook-form";
import Iconify from "../../../../components/Iconify";
import useIsMountedRef from "../../../../hooks/useIsMountedRef";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "../../../../redux/store";
import {
  postRecharge,
  startLoading,
  stopLoading,
} from "../../../../redux/slices/wallet";
import useAuth from "../../../../hooks/useAuth";
// ----------------------------------------------------------------------

Recharge.propTypes = {
  isOpen: PropTypes.bool,
  onCancel: PropTypes.func,
};

export default function Recharge({ isOpen, onCancel }) {
  const dispatch = useDispatch();
  const { user, refresh } = useAuth();
  const isMountedRef = useIsMountedRef();
  const { enqueueSnackbar } = useSnackbar();
  const { t } = useTranslation();

  const { isLoading } = useSelector((state) => state.wallet);

  const [cardType, setCardType] = useState(null);

  // Extend the schema to validate card inputs
  const UpdateUserSchema = Yup.object().shape({
    method: Yup.string().required("Payment method is required"),
    montant: Yup.number().required("Amount is required"),
    cardNumber: Yup.string().when("method", {
      is: "Credit Card",
      then: Yup.string()
        .matches(/^\d{16}$/, "Invalid card number")
        .required("Card number is required"),
    }),
    expiryDate: Yup.string().when("method", {
      is: "Credit Card",
      then: Yup.string()
        .matches(
          /^(2[0-9])\/(0[1-9]|1[0-2])$/,
          "Invalid expiry date (format should be YY/MM)"
        )
        .required("Expiry date is required"),
    }),
    cvv: Yup.string().when("method", {
      is: "Credit Card",
      then: Yup.string()
        .matches(/^\d{3}$/, "Invalid CVV")
        .required("CVV is required"),
    }),
  });

  const defaultValues = {
    method: "Credit Card",
    montant: 10,
    cardNumber: "",
    expiryDate: "",
    cvv: "",
  };

  const methods = useForm({
    resolver: yupResolver(UpdateUserSchema),
    defaultValues,
  });

  const {
    watch,
    setError,
    reset,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const values = watch();

  const handleCardType = (value) => {
    const validation = cardValidator.number(value);
    if (validation.card) {
      setCardType(validation.card.type);
    } else {
      setCardType(null);
    }
  };

  useEffect(() => {
    handleCardType(values.cardNumber);
  }, [values.cardNumber]);

  const onSubmit = async (data) => {
    if (data.method !== "Credit Card") {
      enqueueSnackbar("Very Soon!", { variant: "warning" });
      return;
    }
    try {
      dispatch(startLoading());
      const res = await postRecharge(data);
      enqueueSnackbar("Payment success!");
      await refresh();
      reset(defaultValues);
      dispatch(stopLoading());
    } catch (error) {
      console.error(error.response.data);
      enqueueSnackbar(error?.response?.data?.detail, { variant: "error" });
      dispatch(stopLoading());
      if (isMountedRef.current) {
        if (isMountedRef.current) {
          setError("afterSubmit", {
            ...error,
            message: error.response.data?.detail,
          });
        }
      }
    }
  };

  return (
    <Collapse in={isOpen}>
      <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
        <Card sx={{ p: 3 }}>
          <Typography
            variant="overline"
            sx={{ mb: 3, display: "block", color: "text.secondary" }}
          >
            {t("params.deposit_account")}
          </Typography>
          {!!errors.afterSubmit && (
            <Alert severity="error">{errors.afterSubmit.message}</Alert>
          )}
          <Box
            sx={{
              padding: 3,
              borderRadius: 1,
            }}
          >
            <Stack spacing={3}>
              <Stack spacing={2} direction={{ xs: "column", md: "row" }}>
                <RHFSelect
                  name="method"
                  label={t("params.payment_method")}
                  placeholder={t("params.payment_method")}
                >
                  <option value="" />
                  {["Moncash", "Natcash", "Credit Card"].map(
                    (option, index) => (
                      <option key={index} value={option}>
                        {option}
                      </option>
                    )
                  )}
                </RHFSelect>

                <RHFTextField
                  name="montant"
                  label="Montant"
                  placeholder="Montant"
                  type="number"
                />
              </Stack>

              {values.method === "Credit Card" && (
                <Stack spacing={2}>
                  <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
                    <RHFTextField
                      name="cardNumber"
                      label="Card Number"
                      InputProps={{
                        endAdornment: cardType && (
                          <Iconify
                            icon={`logos:${cardType}`}
                            width={24}
                            height={24}
                          />
                        ),
                      }}
                    />
                  </Box>
                  <Stack direction="row" spacing={2}>
                    <RHFTextField
                      name="expiryDate"
                      label="Expiry Date (YY/MM)"
                    />
                    <RHFTextField name="cvv" label="CVV" type="text" />
                  </Stack>
                </Stack>
              )}

              <Stack direction="row" justifyContent="flex-end" spacing={1.5}>
                <Button color="inherit" variant="outlined" onClick={onCancel}>
                  {t("params.cancel")}
                </Button>
                <LoadingButton
                  loading={isLoading}
                  type="submit"
                  variant="contained"
                >
                  {t("params.recharge")}
                </LoadingButton>
              </Stack>
            </Stack>
          </Box>
        </Card>
      </FormProvider>
    </Collapse>
  );
}
